import React from 'react';
import { useForm as useReactHookForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import {
  Box,
  TextField,
  Typography,
  Grid,
  Alert,
  FormControlLabel,
  Checkbox,
  Paper,
  InputAdornment,
} from '@mui/material';
import { Home, LocationOn } from '@mui/icons-material';
import { useForm } from '../../context/FormContext';
import { addressInfoSchema } from '../../utils/validationSchemas';
import AnimatedWrapper from '../AnimatedWrapper';

const AddressInfoStep = () => {
  const { formData, updateFormData } = useForm();
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
    watch,
    setValue,
  } = useReactHookForm({
    resolver: yupResolver(addressInfoSchema),
    defaultValues: formData.addressInfo,
    mode: 'onChange',
  });

  const watchedValues = watch();
  const sameAsRegistration = watch('sameAsRegistration');

  //     
  React.useEffect(() => {
    updateFormData('addressInfo', watchedValues);
  }, [watchedValues, updateFormData]);

  //      
  const handleSameAsRegistrationChange = (event) => {
    const checked = event.target.checked;
    setValue('sameAsRegistration', checked);
    
    if (checked) {
      setValue('actualAddress', watchedValues.registrationAddress);
    } else {
      setValue('actualAddress', '');
    }
  };

  return (
    <AnimatedWrapper>
      <Box>
        <Typography variant="h5" sx={{ mb: 3, color: 'primary.main', fontWeight: 600 }}>
           
        </Typography>
        
        <Alert 
          severity="info" 
          sx={{ 
            mb: 4,
            borderRadius: 2,
            '& .MuiAlert-icon': { fontSize: '1.25rem' },
          }}
        >
                   .
        </Alert>

        <form onSubmit={handleSubmit(() => {})}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                {...register('registrationAddress')}
                fullWidth
                label="  *"
                placeholder=". , . -7, . 8, . 25"
                multiline
                rows={2}
                error={!!errors.registrationAddress}
                helperText={errors.registrationAddress?.message || '   '}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start" sx={{ alignSelf: 'flex-start', mt: 1 }}>
                      <Home color="action" />
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      '& .MuiOutlinedInput-notchedOutline': {
                        borderColor: 'primary.main',
                      },
                    },
                  },
                }}
              />
            </Grid>

            <Grid item xs={12}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={sameAsRegistration || false}
                    onChange={handleSameAsRegistrationChange}
                    color="primary"
                  />
                }
                label="     "
                sx={{ 
                  '& .MuiFormControlLabel-label': { 
                    fontSize: '0.95rem',
                    fontWeight: 500,
                  } 
                }}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                {...register('actualAddress')}
                fullWidth
                label={sameAsRegistration ? ' ' : '  *'}
                placeholder=". , .  , . 85, . 12"
                multiline
                rows={2}
                disabled={sameAsRegistration}
                error={!!errors.actualAddress}
                helperText={
                  sameAsRegistration 
                    ? '    '
                    : errors.actualAddress?.message || '  '
                }
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start" sx={{ alignSelf: 'flex-start', mt: 1 }}>
                      <LocationOn color="action" />
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      '& .MuiOutlinedInput-notchedOutline': {
                        borderColor: 'primary.main',
                      },
                    },
                  },
                }}
              />
            </Grid>
          </Grid>

          {/*   */}
          <Paper 
            sx={{ 
              mt: 4, 
              p: 3, 
              bgcolor: 'grey.50', 
              borderRadius: 2,
              border: '1px solid',
              borderColor: 'grey.200',
            }}
          >
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
              <strong>  :</strong>
            </Typography>
            <Typography variant="body2" color="text.secondary">
                     <br/>
                       <br/>
                  : , /, , , <br/>
                       
            </Typography>
          </Paper>

          {/*    */}
          <Box sx={{ mt: 3 }}>
            {isValid ? (
              <Alert 
                severity="success"
                sx={{ 
                  borderRadius: 2,
                  '& .MuiAlert-icon': { fontSize: '1.25rem' },
                }}
              >
                    
              </Alert>
            ) : (
              <Alert 
                severity="warning"
                sx={{ 
                  borderRadius: 2,
                  '& .MuiAlert-icon': { fontSize: '1.25rem' },
                }}
              >
                ,     
              </Alert>
            )}
          </Box>
        </form>
      </Box>
    </AnimatedWrapper>
  );
};

export default AddressInfoStep; 