import * as yup from 'yup';

//   
const phoneRegex = /^\+7 \(\d{3}\) \d{3} \d{2} \d{2}$/;
const passportSeriesRegex = /^\d{2,4}$/;
const passportNumberRegex = /^\d{7,8}$/;

//     
export const personalInfoSchema = yup.object({
  firstName: yup
    .string()
    .required('   ')
    .min(2, '    2 ')
    .matches(/^[--\s]+$/, '     '),
  lastName: yup
    .string()
    .required('   ')
    .min(2, '    2 ')
    .matches(/^[--\s]+$/, '     '),
  middleName: yup
    .string()
    .min(2, '    2 ')
    .matches(/^[--\s]*$/, '     '),
  birthDate: yup
    .date()
    .required('    ')
    .max(new Date(Date.now() - 18 * 365 * 24 * 60 * 60 * 1000), '     18 ')
    .min(new Date(Date.now() - 100 * 365 * 24 * 60 * 60 * 1000), '    100 '),
  phone: yup
    .string()
    .required('   ')
    .matches(phoneRegex, '  . : +7 (776) 531 16 00'),
  email: yup
    .string()
    .required('Email   ')
    .email('  email'),
});

//     
export const passportInfoSchema = yup.object({
  series: yup
    .string()
    .required('    ')
    .matches(passportSeriesRegex, '   2-4 '),
  number: yup
    .string()
    .required('    ')
    .matches(passportNumberRegex, '   7-8 '),
  issuedBy: yup
    .string()
    .required('    ')
    .min(10, ' 10 '),
  issueDate: yup
    .date()
    .required('    ')
    .max(new Date(), '      '),
  birthPlace: yup
    .string()
    .required('    ')
    .min(5, ' 5 '),
});

//     
export const addressInfoSchema = yup.object({
  registrationAddress: yup
    .string()
    .required('    ')
    .min(10, ' 10 '),
  actualAddress: yup
    .string()
    .when('sameAsRegistration', {
      is: false,
      then: (schema) => schema.required('    ').min(10, ' 10 '),
      otherwise: (schema) => schema.notRequired(),
    }),
  sameAsRegistration: yup.boolean(),
});

//      
export const workInfoSchema = yup.object({
  company: yup
    .string()
    .required('    ')
    .min(3, ' 3 '),
  position: yup
    .string()
    .required('   ')
    .min(3, ' 3 '),
  workExperience: yup
    .number()
    .required('    ')
    .min(0, '    ')
    .max(50, '    50 '),
  monthlyIncome: yup
    .number()
    .required('    ')
    .min(50000, '  50,000 ')
    .max(50000000, '  50  '),
  additionalIncome: yup
    .number()
    .min(0, '     ')
    .max(50000000, '   50  '),
});

//     
export const creditInfoSchema = yup.object({
  amount: yup
    .number()
    .required('    ')
    .min(500000, '   500,000 ')
    .max(100000000, '   100,000,000 '),
  term: yup
    .number()
    .required('    ')
    .min(6, '  6 ')
    .max(360, '  360 '),
  purpose: yup
    .string()
    .required('    '),
  hasOtherCredits: yup.boolean(),
  otherCreditsInfo: yup
    .string()
    .when('hasOtherCredits', {
      is: true,
      then: (schema) => schema.required('    '),
      otherwise: (schema) => schema.notRequired(),
    }),
});

//    
export const agreementsSchema = yup.object({
  dataProcessing: yup
    .boolean()
    .oneOf([true], '     '),
  creditConditions: yup
    .boolean()
    .oneOf([true], '    '),
  notifications: yup.boolean(),
});

//     
export const fullFormSchema = yup.object({
  personalInfo: personalInfoSchema,
  passportInfo: passportInfoSchema,
  addressInfo: addressInfoSchema,
  workInfo: workInfoSchema,
  creditInfo: creditInfoSchema,
  agreements: agreementsSchema,
}); 